import React, {Component} from 'react';
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Button from "react-bootstrap/Button";
import Bandwidth_measurement_collector from "../tools/bandwidth_measurement_collector";


export default class BitrateStreamSample extends Component {
  render() {
    return (
      <>
        <Navbar bg="light">
          <Navbar.Brand href="#">KNP</Navbar.Brand>
        </Navbar>
        <Container>
          <Row>
            Hello, World!
          </Row>
          <Button onClick={this.download.bind(this)}>download</Button>
          <Button onClick={this.cancel.bind(this)}>cancel</Button>
        </Container>
      </>
    );
  }

  async download() {
    const meas_callback = (measurement) => {
      console.log(`rate ${measurement/1e6}mbps`);
    },
    done_callback = (measurement) => {
      console.log(`Done rate ${measurement/1e6}mbps`);
    };

    this.downloader = new Bandwidth_measurement_collector(meas_callback, done_callback)
    const res = await this.downloader.measure_download(100*1e6, 4);
    console.log("done with downloading", res/1e6)
  }

  cancel() {
    console.log("cancelled");
    this.downloader.cancel();
  }

  async download2() {
    let size = 100 * 1e6;
    //let size = 10;
    let res = await fetch(`https://speed1.arshankhanifar.workers.dev/hi/down?bytes=${size}`);
    const reader = res.body.getReader();
    const window_size = 1e6;
    let window = 0;

    let start = Date.now(),
      total_start = start;

    const stream = new ReadableStream({
      start(controller) {
        return pump();
        function pump() {
          return reader.read().then(({ done, value }) => {
            // When no more data needs to be consumed, close the stream
            if (done) {
              console.log("shits done");
              const total_speed = size/(Date.now() - total_start);
              const total_speed_mbps = total_speed * 8 / 1000;
              console.log("average speed:", total_speed_mbps);
              controller.close();
              return;
            }
            // Enqueue the next data chunk into our target stream
            window += value.length;
            if (window > window_size) {
              let speed = window/(Date.now() - start);
              let speed_mbitps = speed * 8/ 1000;
              console.log(`speed: ${speed_mbitps}mbps`);
              window = 0;
              start = Date.now();
            }
            return pump();
          });
        }
      }
    });
  }
}
