import Stage from "./stage";
import Bandwidth_measurement_collector from "../tools/bandwidth_measurement_collector";
import LatencyCollectorProvider from "../tools/latency_collector_provider";
import {AppConfiguration} from "../app_configuration";


export default class DownlinkMeasurementStage extends Stage {
  DOWNLOAD_DURATION = 10;
  constructor(data_callback, bandwidth_callback, done_callback) {
    super();
    this.data_callback = data_callback;
    this.bandwidth_callback = bandwidth_callback;
    this.done_callback = done_callback;
  }

  async run() {
    const MEASURE_LATENCY_FOREVER = 1e5;
    super.run();
    const collector = LatencyCollectorProvider.provide(this.data_callback, null, MEASURE_LATENCY_FOREVER);
    collector.start();
    const meas_callback = (measurement) => {
        console.log(`realtime rate ${measurement/1e6}mbps`);
    };
    const downloader = new Bandwidth_measurement_collector(meas_callback);
    let download_speed = await downloader.download_for_n_seconds(
      this.DOWNLOAD_DURATION,
      AppConfiguration.MAX_NUM_STREAMS
    );
    collector.cancel();
    this.bandwidth_callback(download_speed);
    this.done_callback();
  }
}