import {AppConfiguration} from "../app_configuration";
import GenericLatencyCollector from "./generic_latency_collector";
import {main_candidate} from "./latency_candidates";

export default class LatencyCollectorProvider {
  static provide(data_cb, done_cb, count) {
    return new GenericLatencyCollector(
      main_candidate,
      AppConfiguration.PING_INTERVAL,
      data_cb,
      done_cb,
      count
    );
  }
}