import Stage from "./stage";
import LatencyCollectorProvider from "../tools/latency_collector_provider";


export default class LatencyMeasurementStage extends Stage {
  NUM_MEASUREMENTS = 30;
  constructor(data_callback, done_callback) {
    super();
    this.data_callback = data_callback;
    this.done_callback = done_callback;
  }

  async run() {
    super.run();
    const collector = LatencyCollectorProvider
      .provide(this.data_callback, this.done_callback, this.NUM_MEASUREMENTS);
    await collector.start();
  }
}