import Timer from "../utilities/timer";

export default class Bandwidth_measurement_collector {
  constructor(measurement_callback, done_callback) {
    this.measurement_callback = measurement_callback || (() => null);
    this.done_callback = done_callback || (() => null);
    this.controller = new AbortController();
  }

  get_http_fetch(size, i, down) {
    let signal = this.controller.signal;
    if (down) {
      return fetch(`https://speed${i+1}.arshankhanifar.workers.dev/hi/down?bytes=${size}`, {signal});
    } else {
      let url = `https://speed${i+1}.arshankhanifar.workers.dev/hi/up`;
      return fetch(`${url}`, {...{
          method: 'POST',
          body: '0'.repeat(size),
          headers: { 'content-length': size }
        }, signal});
    }
  }

  convert_to_bps(r) {
    return r*1000*8;
  }

  async download(size, streams) {
    return this.measure_download(size, streams)
  }

  async download_for_n_seconds(n, streams) {
    const MAX_DOWNLOAD_SIZE = 500 * 1e6;
    setTimeout(() => {this.cancel()}, n * 1000);
    return this.measure_download(MAX_DOWNLOAD_SIZE, streams);
  }

  make_requests(num_streams, size, download) {
    return Array(num_streams)
      .fill(0)
      .map(_ => { return {} })
      .map((req, i) => {
        req.fetch = () => this.get_http_fetch(size, i, download);
        return req;
      })
  }

  async measure_upload(size, streams) {
    let start = Date.now();
    let requests = this.make_requests(streams, size, false)
      .map((req) => req.fetch().then(res=>res.text()));
    await Promise.all(requests);
    return this.convert_to_bps((size * streams)/(Date.now() - start));
  }

  async measure_download(size, streams) {
    const window_size = 1e6,
      total_start = Date.now();
    let start = Date.now(),
      counter = 0,
      total_counter = 0;

    const reset_counter = () => {
      if (counter < window_size) {
        return;
      }
      let speed = this.convert_to_bps(counter / (Date.now() - start));
      this.measurement_callback(speed);
      counter = 0;
      start = Date.now();
    };

    let requests = this.make_requests(streams, size, true)
      .map(async (req) => {
        const res = await req.fetch();
        const reader = res.body.getReader();
        return new Promise(async resolve => {
          try {
            await pump();
          } catch (err) {
            resolve();
          }
          function pump() {
            return reader.read().then(({ done, value }) => {
              if (done) {
                resolve();
                return;
              }
              counter += value.length;
              total_counter += value.length;
              reset_counter();
              return pump();
            });
          }
        });
      });
    await Promise.all(requests);
    let rate_bps = this.convert_to_bps(total_counter/(Date.now() - total_start));
    this.done_callback(rate_bps);
    return rate_bps;
  }

  cancel() {
    this.controller.abort();
  }
}